.navColumn {
    
    width:10%;
    height:100vh;
    margin-top: -120px;
    text-align:center;
   

}

.navColumn img {
    width:50%;

    margin-bottom: 20px;
    
    
}

nav ul {
    padding:0;

    text-align: center;
    list-style: none;
}

nav li {
    padding:10px 0;
    font-weight: 500;
}

nav li:hover {
    color:rgb(0, 162, 255);
    cursor: pointer;
}

.link {
    text-decoration:none;
}

.link:visited {
    color:#000;
    color:initial;
}
@media (max-width: 1366px)

{
    
.navColumn img {
    margin-top: 10px;
}

   

    

    
}

@media (max-width: 1200px)

{
    
.navColumn img {
    margin-top: 18px;
}

}

@media (max-width: 992px)

{
    
.navColumn img {
    width:60%;
}
   

    

    
}

@media (max-width: 768px)

{

    .navColumn img {
        margin-top: 28px;
    }
    .navColumn {
        width: 16%;
       
    }

    
}

@media (max-width: 540px)

{
    .navColumn {
        width:100%;
        height: 20px;
        margin-bottom:30px;
        
    }
    .navColumn img {
        display:none;
        
    }
    

    .navColumn ul {
        display:flex;
        justify-content: space-around;
        
        
    }

   

    

    
}

@media (max-width: 460px)

{
    .navColumn {
        margin-top: 0;
 
    }

   

    

    
}


.header {
    
    text-align: center;
    margin-left: 10%;
    width:90%;
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding:30px 0;
    
    
    
   

    
    

}

.header .bars {
    display:none;
}

label #btn,
    label #cancel {
        cursor: pointer;
        display:none;
    }

    #check {
        display:none;
    }

.header img {
    width:100px;
}

.header h1 {
    font-family: 'Mrs Saint Delafield', cursive;
    font-weight: 300;
    font-size: 60px;
    word-spacing: 10px;
    margin-bottom: -10px;
    margin-left: 80px;
    flex-grow: 2;
}

.header ul {
    
    list-style: none;
    
    padding-bottom: 10px;
    padding-left: 0;
    margin-right: 30px;
}

.header ul li {
    display:inline-block;
    
}

.header ul li:last-child {
    
    margin-left: 10px;
}

.header ul li:hover {
    transform: scale(1.2);
    transition:0.4s;
}



.header a {
    color:#000;
    color:initial;
    padding:0 10px;
}

.header a:hover {
    color:rgb(0, 162, 255);
    cursor: pointer;
    
    transition:0.4s;
}
@media (max-width: 768px)

{
    .header {
    
       
        margin-left: 10%;
        width:90%;
        
        padding:40px 0;
        
    }

    .header h1 {
       
        font-size: 40px;
        word-spacing: 10px;
        margin-bottom: -10px;
        margin-left: 80px;
        flex-grow: 2;
    }

    .header ul {
    
    list-style: none;
    
    padding-bottom: 0;
    
    margin:0;
}

.header ul li svg {
    
    height: 30px;
    width:30px;
}

.header ul li:last-child {
    
    margin-left: 0px;
}

    
}

@media (max-width: 540px)

{
    .header {
        display:flex;
        padding:30px 0;
        margin:0 0;
        width:100%;
        flex-grow: none;
        
        
    }
    

    .header h1 {
        font-size: 50px;
        word-spacing: 2px;
        margin-bottom: none;
        margin-left: none;
        margin-right: 80px;
        
       
    }

    .header ul {
        display:none;
        margin:0;
    }
     
    
    

    
    
}

@media (max-width: 460px)

{
    .header {
        margin-left: 0;
    }
    

    .header h1 {
        
        font-size: 45px;
        word-spacing: 2px;
        margin-left:0;
        margin-right: 0;
        
        
       
    }
    

    
    
}



.content {
    
    padding:60px 0;
    
    background-image: url(/Painter-React-Website/static/media/canvas.8ad9a46e.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width:90%;

}

.content p {
    font-weight: 500;
    
}

.row1 {
    padding-top: 60px;
}

.row1,
.row2 {
    margin-bottom: 80px;
    width:100%;
    text-align: center;
    
    ;
    
    
}

.row3 {
    width:90%;
    text-align: left;
    
    margin-left: 100px;
}

.row1 .photo:first-child,
.row2 .photo:first-child,
.row3 .photo:first-child {
    margin-left:0;
    
}

figcaption {
    width:100%;
    
    
}

.row3 figcaption {
    text-align: center;
}
.photo {
    display:inline-block;
    width:20%;
    margin-left: 60px;
    vertical-align: top;
}

.photo figcaption {
        padding-top: 10px;
        font-weight: bold;
        font-size: 18px;
}

.photo figcaption span {
    color:blue;
    background-color: rgba(0, 0, 0, 0.281);
}

.photo figcaption em {
    font-weight: 500;
}



.content img {
    width:100%;
    
    border-radius:10px;
box-shadow: 0px 0px 6px 5px rgba(69,69,69,1);
}

.firstImage {
    margin-left: 0;
}

.content img:hover {
    cursor: pointer;
    transform: scale(1.05);
    transition:0.4s;
    -webkit-filter: grayscale(50%);
            filter: grayscale(50%);
    
}
@media (max-width: 1366px)
{
    .row1,
    .row2 {
        padding:0 20px 0 20px;
        width:auto;
        margin-right: 0;
    }
    .row3 {
        margin:0;
    }
  
}
@media (max-width: 1200px)
{
    .photo {
        width:19%;
    }

    .row3 {
        text-align: center;
    }

    
}

@media (max-width: 992px)

{
    .row1,
    .row2 {
        margin-bottom: 50px;
    }
    .photo {
        width:18%;
    }
    .photo figcaption {
        
        font-size: 14px;
    }

    
}

@media (max-width: 768px)

{
    .row1,
    .row2,
    .row3 {
        display:flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        padding:0;
    }
    .row3 {
        width:100%;
    }

    .row1 .photo:nth-child(1),
    .row1 .photo:nth-child(2),
    .row2 .photo:nth-child(1),
    .row2 .photo:nth-child(2),
    .row3 .photo:nth-child(1),
    .row3 .photo:nth-child(2)
     {
        margin-bottom: 40px;
    }
    .row1 .photo:nth-child(3),
    .row2 .photo:nth-child(3)
     {
        margin:0;
        
    }

    .row3 .photo:nth-child(3) {
        
        margin:0;
        
        justify-content: start;
        
    }
    .photo {
        width:40%;
    }
    .photo figcaption {
        
        font-size: 14px;
    }

    
}

@media (max-width: 540px)

{
    .content {
    
        
        width:100%;
    
    }
}


@media (max-width: 370px)

{
    
    .row1,
    .row2,
    .row3 {
        display:flex;
        flex-wrap: nowrap;
        flex-direction: column;
        justify-content: center;
        padding:0;
    }

    .row1 .photo:first-child,
    .row2 .photo:first-child,
    .row3 .photo:first-child {
    margin:0 auto;
    margin-bottom: 80px;
    
    }

    .row1 .photo:nth-child(3), .row2 .photo:nth-child(3) {
    margin: 0 auto;
    margin-bottom: 80px;
    }

    .row3 .photo:nth-child(3) {
    margin: 0 auto;
    margin-bottom: 0px;

    }
    

    .photo {
    
        width:90%;
        margin:0 auto;
        
    }

    
    
}
.aboutpage {
    background-image: url(/Painter-React-Website/static/media/canvas.8ad9a46e.jpg);
    background-size: cover;
