    padding:40px 0;
}

.divider {
    width:20%;
    margin:10px auto;
    
}

.socialmedia {
    display:flex;
    justify-content: space-evenly;
    margin-top: 40px;
}

.socialmedia a {
    width:50%;
}

.socialmedia a img:hover {
    transform: scale(1.03);
    transition:0.8s;
}

.socialmedia img {
    
    width:60%;
    box-shadow: 5px 5px 5px 0px rgba(184,184,184,1);
}

.icon {
    
    display:flex;
    justify-content: space-evenly;
    margin-top: 40px;
    color:green;
}
.icon a {
    color:inherit;
}

.icon div {
    width:50%;
}

.icon a svg {
    font-size: 50px;
}

.icon svg:hover {
    transform: scale(1.1);
    transition:0.2s;
    cursor: pointer;
    color:rgb(0, 119, 255);
}


.contacts button {
    padding: 10px 40px;
    border-radius: 10px;
    border:none;
    background-color: green;
    color:white;
    text-transform: uppercase;
    margin-top: 60px;
}

.contacts button:hover {
    cursor: pointer;
    background-color: red;
    transition: 0.3s;
    transform: scale(1.02);
}



